@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,600;0,900;1,200;1,400;1,600;1,900&display=swap);
.button{background-color:#791186;border:none;border-radius:3px;color:#efefed;font-family:"Poppins",sans-serif;font-weight:600;min-width:25rem;padding:0.8rem 2rem;text-transform:uppercase;transition:0.18s ease-in}.button--open{background-color:#4b4249}.button:focus{outline:1px solid #b504cc}.button__icon{color:#efefed;font-size:1.6rem;margin:0 1rem -0.3rem 0;transition:0.25s ease-in-out}.button__icon--open{transform:rotate(135deg)}

.footer{background:rgba(26,25,39,0.5);bottom:0;color:#747b96;display:flex;font-size:1.2rem;justify-content:space-between;padding:1rem 1.5rem;position:absolute;width:100%}.footer__about-link{color:#efefed}

.header{align-items:center;background:rgba(26,25,39,0.5);display:flex;flex-direction:column;padding:1.5rem;position:absolute;text-align:center;top:0;width:100%}

.task{background-color:rgba(66,132,190,0.5);margin-bottom:0.5rem;max-width:40rem;padding:0.8rem 2rem}.task--reminder{background-color:rgba(17,180,245,0.6)}.task__title{display:flex;justify-content:space-between}.task__icon{color:#141346;margin-left:0.8rem}

.about-container{margin-top:11rem}

.dashboard{background:#1a1927;margin:15rem auto;padding:1.5rem;width:95%}

html{box-sizing:border-box;background:linear-gradient(90deg, #53586c 0%, #272938 100%);color:#efefed}*,*::before,*::after{box-sizing:inherit}body{margin:0}body *{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}html{font-size:62.5%}body{font-family:"Poppins",sans-serif;font-size:1.6rem;font-style:normal;font-weight:normal;line-height:1.5}h1,.h1{font-size:3.6rem}

